import React, {useState} from 'react';
import { FormControl, InputLabel, Select, ListItemText, Input, IconButton, Checkbox, TextField, MenuItem, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye, EyeOff, X } from 'react-feather';

export default function AddGroup() {
  const [open, setOpen] = useState(false);
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setPersonName([]);
    setGroupName([]);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const users = [
    'hansen@gmail.com',
    'henry@gmail.com',
    'tucker@gmail.com',
    'hubbard@gmail.com',
    'alexander@gmail.com',
    'abbott@gmail.com',
    'wagner@gmail.com',
    'wilkerson@gmail.com',
    'andrews@gmail.com',
    'snyder@gmail.com',
  ];

  const groups = [
    'Group A',
    'Group B',
    'Group C',
    'Group D',
    'Group E',
    'Group F',
    'Group G',
  ];

  const [personName, setPersonName] = useState([]);
  const handleChangeName = (event) => {
    setPersonName(event.target.value);
  };

  const handleRemoveName = name => {
    setPersonName(personName.filter(item => item !== name));
    console.log(personName)
 };

  const [groupName, setGroupName] = useState([]);
  const handleChangeGroup = (event) => {
    setGroupName(event.target.value);
  };
 
  const handleRemoveGroup = group => {
    setGroupName(groupName.filter(item => item !== group));
    console.log(groupName)
  }; 

  return (
    <div>
      <Button onClick={handleClickOpen} className="inter" variant="contained" color="primary">Add Group</Button>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">ADD A GROUP</div>
        </div>

        <DialogContent>

          <TextField
            required
            size="small"
            margin="dense"
            label="Group Name"
            type="email"
            fullWidth
          />

          <div className="vms-custom-field vms-mb-xs">
            <TextField 
              required 
              fullWidth
              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              onChange={handleChangePassword('password')}
              id="password" label="Password" 
            />
            <IconButton
              className="vms-field-icon-right"
              size="small"
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
            >
              {values.showPassword ? <Eye/> : <EyeOff />}
            </IconButton>
          </div>

          <FormControl className="vms-full-width">
            <InputLabel>Select User(s)*</InputLabel>
            <Select
              multiple
              value={personName}
              onChange={handleChangeName}
              input={<Input/>}
              renderValue={(selected) => selected.join(', ')}
            >
              {users.map((name) => (
                <MenuItem key={name} value={name}>
                  <Checkbox color="primary" checked={personName.indexOf(name) > -1} />
                  <ListItemText primary={name} />
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <div className={personName.length > 0 ? 'vms-mb-md vms-mt-md' : ''}>
            {personName.map((name, index) => (
              <div className="vms-flex-between dynamic-div" key={name}>
                <div className="item-selected">{name}</div>
                <IconButton size="12px" className="vms-white bg-red" onClick={() => handleRemoveName(name)}>
                  <X />
                </IconButton>
              </div>
            ))}
          </div>

          <FormControl className="vms-full-width">
            <InputLabel>Select Group(s)*</InputLabel>
            <Select
              multiple
              value={groupName}
              onChange={handleChangeGroup}
              input={<Input/>}
              renderValue={(selected) => selected.join(', ')}
            >
              {groups.map((group) => (
                <MenuItem key={group} value={group}>
                  <Checkbox color="primary" checked={groupName.indexOf(group) > -1} />
                  <ListItemText primary={group} />
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <div className={groupName.length > 0 ? 'vms-mb-md vms-mt-md' : ''}>
            {groupName.map((group, index) => (
              <div className="vms-flex-between dynamic-div" key={group}>
                <div className="item-selected">{group}</div>
                <IconButton size="12px" className="vms-white bg-red" onClick={() => handleRemoveGroup(group)}>
                  <X />
                </IconButton>
              </div>
            ))}
          </div>

        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>ADD</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}