import React, { useState } from 'react';
import '../../styles/ScanCamera.scss';
import { Grid, Backdrop }  from '@material-ui/core';
import { Eye, Circle, PlusCircle, XCircle, CheckCircle } from 'react-feather';
import VideoPlayer from '../../components/Shared/VideoPlayer';

export default function ScanCamera () {

  const [camera ] = useState([
    {
      id: 1,
      url: 'https://www.youtube.com/watch?v=QVEp781Welg'
    },
    {
      id: 2,
      url: 'https://www.youtube.com/watch?v=aRgqQe-8zYk'
    },
    {
      id: 3,
      url: 'https://www.youtube.com/watch?v=TDlLTfd52aw'
    },
    {
      id: 4,
      url: 'https://www.youtube.com/watch?v=JFqiSr9A-Go'
    },
    {
      id: 5,
      url: 'https://www.youtube.com/watch?v=Z3_9el8ZN6A'
    },
  ]);

  const [streams] = useState([
    {
      id: 1,
      url: 'xxxxxxxx'
    },
    {
      id: 2,
      url: 'xxxxxxxx'
    },
    {
      id: 3,
      url: 'xxxxxxxx'
    },
  ]);

  const [isPreview, setPreviewOpen ] = useState(false);
  const [selectedPreview, setPreview ] = useState('');
  const [isAddedCam, setAddCam ] = useState(false);
  const [isAddedStream, setAddStream ] = useState(false);

  const handlePreviewOpen = (url) => () => {
    setPreviewOpen(true)
    setPreview(url)
  }

  const handlePreviewClose = () => {
    setPreviewOpen(!isPreview)
    setPreview('')
  }

  return (
    <div className="vms-container">
      <div className="vms-table-title vms-mb-lg">Camera Management</div>
      <Grid container spacing={3}>
        <Grid item lg={7} md={7} sm={7} xs={12}>
          <div className="vms-camera-block">
            <h2><b>AVAILABLE CAMERAS</b></h2>
            {camera.map((camera, index) => (
              <Grid className="vms-mt-sm" container spacing={3} key={camera.id}>
                <Grid item lg={8} md={8} sm={6} xs={12}>
                  <div className="vms-flex-items-center">
                    <Circle className="vms-yellow" size="14px"/> {camera.url}
                  </div>
                </Grid>
                <Grid item lg={2} md={2} sm={2} xs={12}>
                  <div className="vms-flex-items-center action" onClick={handlePreviewOpen(camera.url)}>
                    <Eye className="vms-blue"/>PREVIEW
                  </div>
                </Grid>
                <Grid item lg={2} md={2} sm={2} xs={12}>
                  <div className="vms-flex-items-center action" onClick={() => setAddCam(!isAddedCam)}>
                    <PlusCircle className={isAddedCam ? 'vms-green-2': 'vms-green'}/> ADD
                  </div>
                </Grid>
              </Grid>
            ))}
          </div>
        </Grid>
        <Grid item lg={5} md={5} sm={5} xs={12}>
          <div className="vms-camera-block">
            <h2><b>AVAILABLE STREAMS</b></h2>
            {streams.map((streams, index) => (
              <Grid className="vms-mt-sm" container spacing={3} key={streams.id}>
                <Grid item lg={5} md={5} sm={5} xs={12}>
                  <div className="vms-flex-items-center">
                    <Circle className="vms-yellow" size="14px"/> {streams.url}
                  </div>
                </Grid>
                <Grid item lg={2} md={2} sm={2} xs={12}>
                  <div className="vms-flex-items-center action" onClick={() => setAddStream(!isAddedStream)}>
                    {
                      isAddedStream === true ? <CheckCircle className="vms-yellow"/>
                      : <PlusCircle className="vms-green"/> } ADD
                  </div>
                </Grid>
              </Grid>
            ))}
          </div>
        </Grid>
      </Grid>
      <Backdrop className="vms-backdrop" open={isPreview} onClick={handlePreviewClose}>
        <div className="vms-video-backdrop-container">
          <XCircle onClick={handlePreviewClose}/>
          <VideoPlayer url={selectedPreview} />
          <h3>{selectedPreview}</h3>
        </div>
      </Backdrop>
    </div>
  );
}
