import React, {useState} from 'react';
import { IconButton, Grid, TextField, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye, EyeOff } from 'react-feather';
import { Settings } from '@material-ui/icons';
import '../../styles/AccountSettings.scss';

export default function AccountSettings() {
  const [open, setOpen] = useState(false);
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };


  return (
    <div>
      <div className="vms-dropdown-menu" onClick={handleClickOpen}>
        <Settings/>ACCOUNT SETTINGS
      </div>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header vms-mb-md">
          <div className="vms-dialog-title">ACCOUNT SETTINGS</div>
        </div>
        <DialogContent>
          <Grid container>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Username</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                id="username"
                label="Username"
                type="text"
                fullWidth
              />
            </Grid>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Email</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                id="name"
                label="Email Address"
                type="email"
                fullWidth
              />
            </Grid>  
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Password</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <div className="vms-custom-field vms-mb-xs">
                <TextField 
                  required 
                  fullWidth
                  type={values.showPassword ? 'text' : 'password'}
                  value={values.password}
                  onChange={handleChangePassword('password')}
                  id="password" label="Password" 
                />
                <IconButton
                  className="vms-field-icon-right"
                  size="small"
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                >
                  {values.showPassword ? <Eye/> : <EyeOff />}
                </IconButton>
              </div>
            </Grid>
            <Grid item lg={3} md={3} className="vms-flex-items-center"></Grid>
            <Grid item lg={9} md={9}>
              <div className="generate-password">
                Generate Password
              </div>
            </Grid>
          </Grid>   
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>SAVE CHANGES</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}