import React, { useState } from 'react';
import AddUser from './AddUser';
import ViewUser from './ViewUser';
import EditUser from './EditUser';
import RemoveUser from './RemoveUser'
import MultipleDeleteUser from '../UserManagement/MultipleDeleteUser';
import PropTypes from 'prop-types';
import {Table, Backdrop, IconButton, TableBody, TableCell, TableContainer, TableHead, TablePagination, TableRow, TableSortLabel, Paper, Checkbox } from '@material-ui/core';
import CameraData from '../../assets/data/user.json';
import '../../styles/CameraManagement.scss';
import { Sort } from '@material-ui/icons';
import { XCircle, Slash, CheckCircle } from 'react-feather';

const rows = CameraData;

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

const headCells = [
  { id: 'user', isAction: false, disablePadding: true, label: 'User' },
  { id: 'group', isAction: false, disablePadding: false, label: 'User Group' },
  { id: 'status', isAction: false, disablePadding: false, label: 'Status' },
  { id: 'action', isAction: true, disablePadding: false, label: 'Action' },
];

function EnhancedTableHead(props) {
  const { onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        <TableCell padding="checkbox">
          <Checkbox
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{ 'aria-label': 'Select All User' }}
          />
        </TableCell>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={headCell.isAction ? 'center' : 'left'}
            padding={headCell.disablePadding ? 'none' : 'default'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel className="table-sort-icon"
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
              IconComponent={Sort}
            >
              {headCell.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

export default function UserManagement () {
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('user');
  const [selected, setSelected] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [isPreview, setPreviewOpen ] = useState(false)
  const [selectedPreview, setPreview ] = useState('')


  const handlePreviewClose = () => {
    setPreviewOpen(!isPreview)
    setPreview('')
  }


  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = rows.map((n) => n.user);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, user) => {
    const selectedIndex = selected.indexOf(user);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, user);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (user) => selected.indexOf(user) !== -1;

  return (
    <div className="vms-table">
      <div className="vms-table-toolbar">
        <div className="vms-table-title"> User Management</div>
        <div className="vms-action-left vms-flex-items-center">
          <AddUser/>
          { selected.length === 0 ? null
            : <MultipleDeleteUser />
          }
        </div>
      </div>
      <Paper className="paper">
        <TableContainer>
          <Table
            className="table"
            aria-labelledby="tableTitle"
            aria-label="enhanced table"
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {stableSort(rows, getComparator(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  const isItemSelected = isSelected(row.user);
                  const labelId = `enhanced-table-checkbox-${index}`;
                  return (
                    <TableRow
                      hover
                      role="checkbox"
                      tabIndex={-1}
                      key={row.user}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox 
                          color="primary"
                          onClick={(event) => handleClick(event, row.user)}
                          checked={isItemSelected}
                          inputProps={{ 'aria-labelledby': labelId }}
                        />
                      </TableCell>
                      <TableCell component="th" id={labelId} scope="row" padding="none">
                        {row.user}
                      </TableCell>
                      <TableCell align="left">{row.group}</TableCell>
                      <TableCell align="left">{row.status}</TableCell>
                      <TableCell align="center">
                        <div className="vms-flex vms-justify-center">
                          <ViewUser email={row.email} name={row.name} group={row.group} username={row.username}/>
                          <EditUser/>
                          <RemoveUser/>
                          <IconButton aria-label="block">
                            { row.status === 'Blocked' ?  <CheckCircle fontSize="small" className="vms-yellow-2" />
                            : <Slash fontSize="small" className="vms-yellow" /> 
                            }                     
                          </IconButton>
                        </div>
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
      <Backdrop className="vms-backdrop" open={isPreview} onClick={handlePreviewClose}>
        <div className="vms-video-backdrop-container">
          <XCircle onClick={handlePreviewClose}/>
          {/* <VideoPlayer url={selectedPreview} /> */}
          <h3>{selectedPreview}</h3>
        </div>
      </Backdrop>
    </div>
  );
}
