import React from 'react';
import { Share } from '@material-ui/icons';
import { Download } from 'react-feather';
import { IconButton } from '@material-ui/core';
import VideoPlayer from '../../components/Shared/VideoPlayer';
import '../../styles/RecordPreview.scss'

export default function PreviewRecord (props) {
  return (
    <div className="record-preview">
      <VideoPlayer url={props.url}/>
      <div className="info">
        <div className="vms-flex-between">
          <h3 className="vms-blue">{props.name}</h3>
          <div className="actions">
            <IconButton>
              <Share className="vms-green"/>
            </IconButton>
            <IconButton>
              <Download className="vms-yellow"/>
            </IconButton>
          </div>
          
        </div>
        <p><span>Assigned to group : </span>{props.group}</p>
        <p><span>Duration : </span>{props.duration}</p>
        <p><span>Date Recorded : </span>{props.date}</p>
        <p><span>Time Recorded : </span>{props.time}</p>
        <p><span>Save to : </span>{props.location}</p>
      </div>
    </div>
  );
}