import React from 'react';
import { NavLink } from 'react-router-dom';
import { VideoIcon, FileVideoIcon, UserCircleIcon, PlayCircleIcon, LayerGroupIcon } from 'react-line-awesome';
import '../../styles/Subnavigation.scss';
import { InputAdornment, TextField } from '@material-ui/core';
import Search from '@material-ui/icons/Search';

export default function Subnavigation () {
  return (
    <div className="vms-subnavigation">
      <div className="vms-navigation-content">
        <div className="sub-menu-container">
          <NavLink to="/stream" activeClassName="active" className="vms-flex">
            <div className="menu-item">
              <VideoIcon className="sub-menu-icon"></VideoIcon>
            </div>
          </NavLink>
          <NavLink to="/camera-management" activeClassName="active">
            <div className="menu-item">
              <FileVideoIcon className="sub-menu-icon vms-mr-sm"/>
              Camera Management
            </div>
          </NavLink>
          <NavLink to="/record-management" activeClassName="active">
            <div className="menu-item">
              <PlayCircleIcon className="sub-menu-icon vms-mr-sm"/>
              Record Management
            </div>
          </NavLink>
          <NavLink to="/user-management" activeClassName="active">
            <div className="menu-item">
              <UserCircleIcon className="sub-menu-icon vms-mr-sm"/>
              User Management
            </div>
          </NavLink>
          <NavLink to="/group-management" activeClassName="active">
            <div className="menu-item">
              <LayerGroupIcon className="sub-menu-icon vms-mr-sm"/>
              Group Management
            </div>
          </NavLink>
        </div>
        <div className="vms-search-area">
          <TextField
              id="vms-search-field"
              variant="outlined"
              placeholder="Search"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Search />
                  </InputAdornment>
                ),
              }}
            />
        </div>
      </div>
    </div>
  );
}
