import React, {useState} from 'react';
import { IconButton, TextField, MenuItem, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye, EyeOff } from 'react-feather';
import { Link } from 'react-router-dom';

const dummyGroups = [
  {
    value: 'A',
    label: 'Group A',
  },
  {
    value: 'B',
    label: 'Group B',
  },
  {
    value: 'C',
    label: 'Group C',
  },
  {
    value: 'D',
    label: 'Group D',
  },
];

export default function AddCamera() {
  const [open, setOpen] = useState(false);
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [group, setGroup] = useState('A');

  const handleChangeGroup = (event) => {
    setGroup(event.target.value);
  };


  return (
    <div>
      <Button onClick={handleClickOpen} className="inter" variant="contained" color="primary">Add Camera</Button>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">ADD CAMERA</div>
          <div className="vms-text-right">
            <Link to="scan-camera">
              <Button onClick={handleClickOpen} className="inter vms-bg-red vms-text-white vms-px-md" size="small" variant="contained">SCAN</Button>
            </Link>
          </div>
        </div>
        <DialogContent>
          <TextField
            required
            size="small"
            margin="dense"
            id="url"
            label="Camera URL"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            id="name"
            label="Camera Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            id="streamName"
            label="Stream Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            id="name"
            label="Email Address"
            type="email"
            fullWidth
          />

          <TextField
            required
            size="small"
            margin="dense"
            id="username"
            label="Username"
            type="text"
            fullWidth
          />

          <div className="vms-custom-field vms-mb-xs">
            <TextField 
              required 
              fullWidth

              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              onChange={handleChangePassword('password')}
              id="password" label="Password" 
            />
            <IconButton
              className="vms-field-icon-right"
              size="small"
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
            >
              {values.showPassword ? <Eye/> : <EyeOff />}
            </IconButton>
          </div>

          <TextField
            id="groups"
            required 
            fullWidth
            select
            label="Group"
            value={group}
            onChange={handleChangeGroup}
          >
            {dummyGroups.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>

        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>ADD</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}