import React, {useState} from 'react';
import { Button, IconButton, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { StopCircle } from 'react-feather';
import '../../styles/DeleteCamera.scss';


export default function StopRecording() {
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <IconButton aria-label="stop" onClick={handleClickOpen}>
        <StopCircle fontSize="large" className="vms-red action" />
      </IconButton>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">RECORD A CAMERA</div>
        </div>
        <DialogContent className="vms-mt-sm">
          <p><b>This action will STOP the recording of "Record name".</b></p>
          <p>Warning : If there is a schedule in this recording, automatically it will be revoked<br></br> if you wish to stop the recording now.</p>
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>STOP RECORDING</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}