import React from 'react';
import ReactPlayer from 'react-player/lazy';

export default function VideoPlayer(props) {
  return (
    <div className="vms-player-container">
      <ReactPlayer 
        className="actual-video"
        url={props.url} 
        controls={true}
        light={false}
        width='100%'
        height='100%'
      />
    </div>
  );
}
