import React, {useState} from 'react';
import { Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Circle } from 'react-feather';
import '../../styles/DeleteCamera.scss';


export default function MultipleDeleteUser() {
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button onClick={handleClickOpen} className="inter vms-ml-xs" variant="contained" color="primary">REMOVE USER</Button>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">REMOVE USER</div>
        </div>
        <DialogContent className="vms-mt-sm">
          <p><b>Are you sure you want to remove camera's in the list ?</b></p>
          <div className="vms-text-center vms-auto-container">
            <ul>
              <li className="vms-flex-items-center">
                <Circle size="12px" className="vms-mr-sm vms-yellow"/> User A
              </li>
              <li className="vms-flex-items-center">
                <Circle size="12px" className="vms-mr-sm vms-yellow"/> User B
              </li>
              <li className="vms-flex-items-center">
                <Circle size="12px" className="vms-mr-sm vms-yellow"/> User C
              </li>
            </ul>
          </div>
          <p>Warning : The user will also be removed from assigned groups and <br></br>video streams.</p>
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>REMOVE</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}