import React, {useState} from 'react';
import { Button, IconButton, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import {  MinusCircle } from 'react-feather';
import '../../styles/DeleteCamera.scss';


export default function RemoveGroup() {
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <IconButton aria-label="delete" onClick={handleClickOpen}>
        <MinusCircle fontSize="small" className="vms-red" />
      </IconButton>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">REMOVE GROUP</div>
        </div>
        <DialogContent className="vms-mt-sm">
          <p><b>Are you sure you want to remove "Group Name" ?</b></p>
          <p>Warning : Assigned users will no longer access the video streams and  <br></br>video files assigned to this group.</p>
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>REMOVE</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}