import React, {useState} from 'react';
import { IconButton, Grid, FormControlLabel, Checkbox, TextField, MenuItem, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye, EyeOff } from 'react-feather';

const dummyGroups = [
  {
    value: 'A',
    label: 'Group A',
  },
  {
    value: 'B',
    label: 'Group B',
  },
  {
    value: 'C',
    label: 'Group C',
  },
  {
    value: 'D',
    label: 'Group D',
  },
];

export default function AddUser() {
  const [open, setOpen] = useState(false);
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [group, setGroup] = useState('A');

  const handleChangeGroup = (event) => {
    setGroup(event.target.value);
  };

  /* Access Rights states */
  const [isAddCamera, setAddCamera] = useState(false);
  const [isEditCamera, setEditCamera] = useState(false);
  const [isRemoveCamera, setRemoveCamera] = useState(false);
  const [isAddRecording, setAddRecording] = useState(false);
  const [isViewRecording, setViewRecording] = useState(false);
  const [isRemoveRecording, setRemoveRecording] = useState(false);
  /* User */
  const [isAddUser, setAddUser] = useState(false);
  const [isEditUser, setEditUser] = useState(false);
  const [isBlockUser, setBlockUser] = useState(false);
  const [isRemoveUser, setRemoveUser] = useState(false);
  /* Groups */
  const [isAddGroups, setAddGroups] = useState(false);
  const [isEditGroups, setEditGroups] = useState(false);
  const [isBlockGroups, setBlockGroups] = useState(false);
  const [isRemoveGroups, setRemoveGroups] = useState(false);

  return (
    <div>
      <Button onClick={handleClickOpen} className="inter" variant="contained" color="primary">Add User</Button>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">ADD USER</div>
        </div>
        <DialogContent>
          <TextField
            required
            size="small"
            margin="dense"
            label="Email"
            type="email"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="First Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="Last Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="User Name"
            type="text"
            fullWidth
          /> 

          <div className="vms-custom-field vms-mb-xs">
            <TextField 
              required 
              fullWidth
              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              onChange={handleChangePassword('password')}
              id="password" label="Password" 
            />
            <IconButton
              className="vms-field-icon-right"
              size="small"
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
            >
              {values.showPassword ? <Eye/> : <EyeOff />}
            </IconButton>
          </div>

          <TextField
            id="groups"
            required 
            fullWidth
            select
            label="Group"
            value={group}
            onChange={handleChangeGroup}
          >
            {dummyGroups.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>

          <div className="access-rights">
            <h4 className="vms-mt-lg">ACCESS RIGHTS</h4>
            <Grid direction="row" container>
              <Grid item lg={6}>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setAddCamera(!isAddCamera)} />}
                  label="Add a Camera"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setEditCamera(!isEditCamera)} />}
                  label="Edit a Camera"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setRemoveCamera(!isRemoveCamera)} />}
                  label="Remove a Camera"
                />
              </Grid>
              <Grid item lg={6}>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setAddRecording(!isAddRecording)} />}
                  label="Add Recording"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setViewRecording(!isViewRecording)} />}
                  label="View a Recording"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setRemoveRecording(!isRemoveRecording)} />}
                  label="Remove a Recording"
                />
              </Grid>
            </Grid>
              <hr/>
            <Grid direction="row" container>
              <Grid item lg={6}>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setAddUser(!isAddUser)} />}
                  label="Add a User"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setEditUser(!isEditUser)} />}
                  label="Edit a User"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setRemoveUser(!isRemoveUser)} />}
                  label="Remove a User"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setBlockUser(!isBlockUser)} />}
                  label="Block/Unblock a User"
                />
              </Grid>
              <Grid item lg={6}>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setAddGroups(!isAddGroups)} />}
                  label="Add a Group"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setEditGroups(!isEditGroups)} />}
                  label="Edit a Group"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setRemoveGroups(!isRemoveGroups)} />}
                  label="Remove a Group"
                />
                <FormControlLabel
                  control={ <Checkbox color="primary" size="small" onClick={() => setBlockGroups(!isBlockGroups)} />}
                  label="Block/Unblock a Group"
                />
              </Grid>   
            </Grid>
          </div>

        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>ADD</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}