import React from 'react';
import { VideoIcon, FileVideoIcon, UserCircleIcon, PlayCircleIcon, LayerGroupIcon, CogIcon} from 'react-line-awesome';
import Notification from '../../components/Notification/Notification';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import '../../styles/Dashboard.scss';
import AccountSettings from '../../components/Settings/AccountSettings';

export default function Dashboard () {
  return (
    <div className="vms-dashboard">
      <Notification/>
      <div className="vms-tile-menu">
        <Grid container
          direction="row"
          justify="space-between"
          alignItems="center"
          spacing={8}
        >
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <Link to="/stream">
              <div className="vms-tile">
                <div className="tile-container">
                  <VideoIcon className="vms-dashboard-icon"/>
                  <h3>STREAMS</h3>
                </div>
              </div>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <Link to="/camera-management">
              <div className="vms-tile">
                <div className="tile-container">
                  <FileVideoIcon className="vms-dashboard-icon"/>
                  <h3>CAMERA MANAGEMENT</h3>
                </div>
              </div>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <Link to="/record-management">
              <div className="vms-tile">
                <div className="tile-container">
                  <PlayCircleIcon className="vms-dashboard-icon"/>
                  <h3>RECORD MANAGEMENT</h3>
                </div>
              </div>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <Link to="/user-management">
              <div className="vms-tile">
                <div className="tile-container">
                  <UserCircleIcon className="vms-dashboard-icon"/>
                  <h3>USER MANAGEMENT</h3>
                </div>
              </div>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <Link to="/group-management">
              <div className="vms-tile">
                <div className="tile-container">
                  <LayerGroupIcon className="vms-dashboard-icon"/>
                  <h3>GROUP MANAGEMENT</h3>
                </div>
              </div>
            </Link>
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={4}>
            <div className="vms-tile">
              <div className="tile-container">
                <CogIcon className="vms-dashboard-icon"/>
                <div className="dashboard-setting-trigger">
                  <AccountSettings/>
                </div>
                <h3>ACCOUNT SETTINGS</h3>
              </div>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
