import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
// Layout
import Header from './layouts/Header/Header';
// Pages
import Dashboard from './pages/Dashboard/Dashboard';
import Stream from './pages/Stream/Stream';
import CameraManagement from './pages/CameraManagement/CameraManagement';
import ScanCamera from './pages/CameraManagement/ScanCamera';
import RecordManagement from './pages/RecordManagement/RecordManagement';
import UserManagement from './pages/UserManagement/UserManagement';
import GroupManagement from './pages/group-management/GroupManagement';
// Auth Pages
import Signin from './pages/Auth/Signin';
import ResetPassword from './pages/Auth/ResetPassword';
import ForgotPassword from './pages/Auth/ForgotPassword';
// Css Files
import './styles/Helper.scss'

function App() {
  return (
    <Router>
      <div className="vms-app">
        <Header/>
        <Switch>
          {/*Auth Routes here */}
          <Route path="/signin" exact component={Signin}/>
          <Route path="/reset-password" exact component={ResetPassword}/>
          <Route path="/forgot-password" exact component={ForgotPassword}/>
          {/* end auth */ }
          <Route path="/" exact component={Dashboard}/>
          <Route path="/stream" exact component={Stream}/>
          <Route path="/camera-management" exact component={CameraManagement}/>
          <Route path="/scan-camera" exact component={ScanCamera}/>
          <Route path="/record-management" exact component={RecordManagement}/>
          <Route path="/user-management" exact component={UserManagement}/>
          <Route path="/group-management" exact component={GroupManagement}/>
        </Switch>
      </div>
    </Router>
  );
}

export default App