import React, {useState} from 'react';
import { Grid, IconButton, TextField, MenuItem, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye, EyeOff } from 'react-feather';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
// https://material-ui-pickers.dev/getting-started/installation

const dummyGroups = [
  {
    value: 'A',
    label: 'Group A',
  },
  {
    value: 'B',
    label: 'Group B',
  },
  {
    value: 'C',
    label: 'Group C',
  },
  {
    value: 'D',
    label: 'Group D',
  },
];

const dummyRecords = [
  {
    value: 'A',
    label: 'Record A',
  },
  {
    value: 'B',
    label: 'Record B',
  },
  {
    value: 'C',
    label: 'Record C',
  },
  {
    value: 'D',
    label: 'Record D',
  },
];


export default function AddRecording() {
  
  const [open, setOpen] = useState(false);
  const [confirm, setConfirmation] = useState({
    initialPrompt: false,
    finalPrompt : false
  });

  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });
  const [selectedDate, setSelectedDate] = React.useState(new Date('2014-08-18T21:11:54'));

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleComfirmOpen = () => {
    setConfirmation({ ...confirm, initialPrompt: true });
  };

  const handleConfirmClose = () => {
    setConfirmation({ ...confirm, initialPrompt: false, finalPrompt: false });
    setOpen(false);
  };

  const handleFinalConfirm = () => {
    setConfirmation({ ...confirm, initialPrompt: true, finalPrompt: true });
  };

  const [group, setGroup] = useState('A');
  const [record, setRecord] = useState('A');

  const handleChangeGroup = (event) => {
    setGroup(event.target.value);
  };

  const handleChangeRecord = (event) => {
    setRecord(event.target.value);
  };


  return (
    <div>
      <Button onClick={handleClickOpen} className="inter" variant="contained" color="primary">ADD RECORDING</Button>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">RECORD CAMERA</div>
        </div>
        <DialogContent>
          <div className="vms-mt-sm">
            <TextField
              id="groups"
              required 
              fullWidth
              select
              label="Select a camera to record"
              value={record}
              onChange={handleChangeRecord}
            >
              {dummyRecords.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
          <div>
            <TextField
              required
              size="small"
              margin="dense"
              id="username"
              label="Recording Name"
              type="text"
              fullWidth
            />
          </div>
          <div className="vms-mt-xs">
            <TextField
              id="groups"
              required 
              fullWidth
              select
              label="Assign User Group(s)"
              value={group}
              onChange={handleChangeGroup}
            >
              {dummyGroups.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
          <div className="vms-mt-xs">
            <div className="vms-custom-field vms-mb-xs">
              <TextField 
                required 
                fullWidth
                label="Assign password"
                type={values.showPassword ? 'text' : 'password'}
                value={values.password}
                onChange={handleChangePassword('password')}
                id="password"
              />
              <IconButton
                className="vms-field-icon-right"
                size="small"
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}
              >
                {values.showPassword ? <Eye/> : <EyeOff />}
              </IconButton>
            </div>
          </div>
          <div>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Grid container
                direction="row"
                spacing={3}
              >
                <Grid item lg={6} md={6}>
                  <KeyboardDatePicker className="vms-full-width"
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="date-picker-start"
                    label="Date picker inline"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </Grid>
                <Grid item lg={6} md={6}>
                  <KeyboardDatePicker className="vms-full-width"
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="date-picker-end"
                    label="End Date"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </Grid>
              </Grid>
            </MuiPickersUtilsProvider>
            <div>
              <TextField
                required
                size="small"
                margin="dense"
                id="saveTo"
                label="Save to"
                type="text"
                fullWidth
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter vms-red" onClick={handleComfirmOpen}>
            <b>START</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>STOP</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
        </DialogActions>
      </Dialog>
      {/* Start Recording prompt */}
      <Dialog className="vms-dialog" open={confirm.initialPrompt} onClose={handleConfirmClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">RECORD CAMERA</div>
        </div>
        <DialogContent>
          <div className="vms-mt-lg vms-mb-lg">
            { !confirm.finalPrompt ?
              <p>This action will start recording "Camera Name"<br></br>
              from "Start Date and Time" to "End Date and Time"</p>
              :
              <p>"Camera Name" is now being recorded.<br></br>
              You can stop the recording anytime by clicking Stop in the Recording Dashboard</p>
            }
          </div>
        </DialogContent>
        <DialogActions className="vms-pb-md">
          { !confirm.finalPrompt ?
          <div>
            <Button className="inter" onClick={handleConfirmClose} color="primary"><b>CANCEL</b> </Button>
            <Button className="inter" onClick={handleFinalConfirm} color="primary"><b>CONFIRM RECORDING</b> </Button>
          </div>
            :   
          <div>
            <Button className="inter" onClick={handleConfirmClose} color="primary"><b>OKAY</b> </Button>
          </div>
          }
        </DialogActions>
      </Dialog>

    </div>
  );
}