import React, {useState} from 'react';
import { IconButton, Grid, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye } from 'react-feather';

export default function ViewUser(props) {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <IconButton aria-label="delete" onClick={handleClickOpen}>
        <Eye fontSize="small" className="vms-blue" />
      </IconButton>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header vms-mb-md">
          <div className="vms-dialog-title">VIEW USER</div>
          <h4 className="vms-green vms-text-right">VIEW STREAM LIST</h4>
          <hr/>
        </div>
        <DialogContent>
          <Grid container>
            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">Email</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> {props.email}</div>
            </Grid>

            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">Name</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> {props.name}</div>
            </Grid>

            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">Username</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> {props.username}</div>
            </Grid>

            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">Group</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> {props.group}</div>
            </Grid>

          </Grid>   
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>EDIT</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>REMOVE</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}