import React, {useState, useRef} from 'react';
import SubNavigation from '../../components/SubNavigation/SubNavigation';
import AccountSettings from '../../components/Settings/AccountSettings';
import '../../styles/Header.scss';
import { Link, useLocation } from 'react-router-dom';
import { BellIcon } from 'react-line-awesome';
import { Badge, Avatar, MenuList, Popper,Grow, ClickAwayListener, Paper } from '@material-ui/core';
import Logo from '../../assets/img/logo.png';
import Profile from '../../assets/img/avatar.png';
import { ExitToApp } from '@material-ui/icons';

export default function Header () {
  const location = useLocation();


  const [open, setOpen] = useState(false);
  const anchorRef = useRef(null);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);


  return (
    <div className="vms-header-parent">
      {/* Main top Menu, Hidden on auth pages */}
      {
        location.pathname.split("/")[1] !== 'signin' && 
        location.pathname.split("/")[1] !== 'forgot-password' && 
        location.pathname.split("/")[1] !== 'reset-password' ?
        <header className="vms-header">
          <div className="vms-header-content">
            <div className="vms-logo">
              <Link to="/">
                <img src={Logo} alt="VMS Logo"/>
              </Link>
            </div>
            <ul>
              <li>
                <Link className="vms-white" to="/signin">Signin</Link>
              </li>
              <li>
                <Link className="vms-white" to="/forgot-password">Forgot Password</Link>
              </li>
              <li>
                <Link className="vms-white" to="/reset-password">Reset Password</Link>
              </li>
              <li>
                <Badge badgeContent={4} color="secondary">
                  <BellIcon className="vms-header-icon"/>
                </Badge>
              </li>
              <li>
                <Avatar alt="Sample Profile" src={Profile} 
                  ref={anchorRef}
                  aria-controls={open ? 'menu-list-grow' : undefined}
                  aria-haspopup="true"
                  onClick={handleToggle}
                />
                <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal className="vms-popper-menu">
                  {({ TransitionProps }) => (
                    <Grow
                      {...TransitionProps}
                    >
                      <Paper>
                        <ClickAwayListener onClickAway={handleClose}>
                          <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                            <AccountSettings/>
                            <div className="vms-dropdown-menu">
                              <ExitToApp/>LOGOUT
                            </div>
                          </MenuList>
                        </ClickAwayListener>
                      </Paper>
                    </Grow>
                  )}
                </Popper>
              </li>
            </ul>
          </div>
      </header>
      : null }
      {/* Show Subnavigation on the main menu */}
      {
        location.pathname.split("/")[1] !== 'stream' && 
        location.pathname.split("/")[1] !== 'camera-management' && 
        location.pathname.split("/")[1] !== 'user-management' && 
        location.pathname.split("/")[1] !== 'scan-camera' &&
        location.pathname.split("/")[1] !== 'group-management' &&  
        location.pathname.split("/")[1] !== 'record-management' ? null
        : <SubNavigation/> }
    </div>
  );
}
