import React, {useState} from 'react';
import { IconButton, Grid, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import { Eye } from 'react-feather';

export default function ViewGroup(props) {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const users = [
    { name : 'Eka', email : 'edecena@gmail.com' },
    { name : 'Eric', email : 'eric@gmail.com' },
    { name : 'Ian', email : 'ian@gmail.com' },
    { name : 'Zandro', email : 'zandro@gmail.com' },
  ];

  const cameras = [
    { name : 'Camera A', url : 'rstp://192.168.2.200/camera/cameraA' },
    { name : 'Camera B', url : 'rstp://192.168.2.200/camera/cameraB' }
  ];

  return (
    <div>
      <IconButton aria-label="delete" onClick={handleClickOpen}>
        <Eye fontSize="small" className="vms-blue" />
      </IconButton>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header vms-mb-md">
          <div className="vms-dialog-title">VIEW A GROUP</div>
        </div>
        <DialogContent>
          <Grid container>
            {/* Group Name */}
            <Grid item lg={3} md={3} className="vms-mt-md">
              <label className="vms-label">Email</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-md">
              <div className="vms-value"> {props.email}</div>
            </Grid>
            
            {/* Assigned Users */}
            <Grid item lg={3} md={3} className="vms-mt-md">
              <label className="vms-label">Assigned Users</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-md">
                { users.map(user =>
                  <Grid container direction="row" className="vms-mb-xs">
                    <Grid item lg={3} md={3}><div className="vms-value"> {user.name} </div></Grid>
                    <Grid item lg={9} md={9}><div className="vms-value"> {user.email}</div></Grid>
                  </Grid>
                )
              }
            </Grid>

             {/* Assigned Camera */}
             <Grid item lg={3} md={3} className="vms-mt-md">
              <label className="vms-label">Assigned Camera</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-md">
                { cameras.map(camera =>
                  <Grid container direction="row" className="vms-mb-xs">
                    <Grid item lg={3} md={3}><div className="vms-value"> {camera.name} </div></Grid>
                    <Grid item lg={9} md={9}><div className="vms-value"> {camera.url}</div></Grid>
                  </Grid>
                )
              }
            </Grid>

          </Grid>   
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleClose} color="primary">
            <b>EDIT</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}