import React, {useState} from 'react';
import { IconButton, TextField, MenuItem, Button, Dialog, DialogActions, DialogContent, Grid, Checkbox, FormControlLabel } from '@material-ui/core';
import { Eye, EyeOff, Edit3 } from 'react-feather';

const dummyGroups = [
  {
    value: 'A',
    label: 'Group A',
  },
  {
    value: 'B',
    label: 'Group B',
  },
  {
    value: 'C',
    label: 'Group C',
  },
  {
    value: 'D',
    label: 'Group D',
  },
];

export default function EditUser() {
  const [open, setOpen] = useState(false);
  const [openList, setOpenList] = useState(false);
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpenList = () => {
    setOpenList(true);
  };

  const handleCloseList = () => {
    setOpenList(false);
  };

  const handleChangePassword = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [group, setGroup] = useState('A');

  const handleChangeGroup = (event) => {
    setGroup(event.target.value);
  };


  return (
    <div>
      <IconButton aria-label="delete" onClick={handleClickOpen}>
        <Edit3 fontSize="small" className="vms-green" />
      </IconButton>
      {/* <Button onClick={handleClickOpen} className="inter" variant="contained" color="primary">Add Camera</Button> */}
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header">
          <div className="vms-dialog-title">EDIT A USER</div>
        </div>
        <DialogContent>
          <TextField
            required
            size="small"
            margin="dense"
            label="Email Address"
            type="email"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="First Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="Last Name"
            type="text"
            fullWidth
          />
          <TextField
            required
            size="small"
            margin="dense"
            label="Username"
            type="text"
            fullWidth
          />
        
          <div className="vms-custom-field vms-mt-xs vms-mb-xs">
            <TextField 
              required 
              fullWidth
              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              onChange={handleChangePassword('password')}
              id="password" label="Password" 
            />
            <IconButton
              className="vms-field-icon-right"
              size="small"
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
            >
              {values.showPassword ? <Eye/> : <EyeOff />}
            </IconButton>
          </div>

          <TextField
            required 
            fullWidth
            select
            label="Group"
            value={group}
            onChange={handleChangeGroup}
          >
            {dummyGroups.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>

        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleOpenList} color="primary">
            <b>EDIT STREAM LIST</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>SAVE</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
        </DialogActions>
      </Dialog>
      {/* Stream list dialog */}
      <Dialog className="vms-dialog" open={openList} onClose={handleCloseList} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header vms-mb-md">
          <div className="vms-dialog-title">USER STREAM LIST</div>
        </div>
        <DialogContent>
          <Grid container>
            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">User Name</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> User E </div>
            </Grid>

            <Grid item lg={3} md={3} className="vms-mt-xs">
              <label className="vms-label">Group</label>
            </Grid>
            <Grid item lg={9} md={9} className="vms-mt-xs">
              <div className="vms-value"> EEE </div>
            </Grid>

            <Grid item lg={12} md={12} className="vms-ml-lg vms-mt-md">
              {/* Loop stream list here */}
              <div>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="medium" />}
                  label="Stream A"
                />
              </div>
              <div>
                <FormControlLabel
                  control={ <Checkbox color="primary" size="medium" />}
                  label="Stream B"
                />
              </div>
            </Grid>
          </Grid>   
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handleCloseList} color="primary">
            <b>CANCEL</b> 
          </Button>
          <Button className="inter" onClick={handleCloseList} color="primary">
            <b>REMOVE ACCESS</b> 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}