
import React, { useState } from 'react';
import VideoPlayer from '../../components/Shared/VideoPlayer';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import Grid from '@material-ui/core/Grid';
import IconGrid from '../../assets/icon/icon-grid.svg';
import IconList from '../../assets/icon/icon-list.svg';
import IconGridActive from '../../assets/icon/icon-grid-active.svg';
import IconListActive from '../../assets/icon/icon-list-active.svg';
import DataVideo from '../../assets/data/camera.json';
import '../../styles/Stream.scss';


export default function Stream () {
  /* Set to list view */
  const [isList, setList] = useState(false);
    const listView = () => {
    setList(true);
    setGrid(false)
  }
  /* Set to grid view */
  const [isGrid, setGrid] = useState(true);
  const gridView = () => {
    setList(false);
    setGrid(true)
  }
  /* Set number of tile count to display */
  const [tileCount, setTileCount] = useState(6);

  /* Get the select tile input value */
  function handleChangeSelect(e) {
    setTileCount(e.target.value);
    console.log(e.target.value + " - " + tileCount);
  }
  
  /* Update stream order when drag using react-beautiful-dnd */
  const [ streams, updateStreamOrder ] = useState(DataVideo);
  function handleOnDragEnd(result) {
    if (!result.destination) return;

    const items = Array.from(streams);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    updateStreamOrder(items);
  }

  return (
    <div className="vms-stream-parent-container">
      <div className="vms-stream">
        <div className="vms-view-filter">
          <div className="tile-number">
            <label className="label">Number of Tiles</label>
            <select value={tileCount} onChange={handleChangeSelect}>
              <option value="3">3</option>
              <option value="6">6</option>
              <option value="9">9</option>
              <option value="12">12</option>
              <option value="16">16</option>
            </select>
          </div>
          <div className="tile-view-option">
            <img onClick={gridView} src={isGrid ? IconGridActive : IconGrid} alt="icon grid"/>
            <img onClick={listView} src={isList ? IconListActive : IconList} alt="icon list"/>
          </div>
        </div>

        <DragDropContext onDragEnd={handleOnDragEnd}>
          <Droppable droppableId="vms-stream-droppable">
            {(provided) => (
              <Grid container 
                className={isList ? 'list-view' : 'vms-stream-item-container'} {...provided.droppableProps} ref={provided.innerRef}
                direction="row"
              >
                {streams.slice(0,tileCount).map(({id, cameraName, streamName, videoUrl }, index) => {
                  return (
                    <Draggable key={id} draggableId={cameraName} index={index}>
                      {(provided) => (
                        <Grid className="stream-item" item xs={12} sm={12} md={isList ? 12 : 4} lg={isList ? 12 : 4}  
                          ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}
                        >
                          <VideoPlayer key={id}
                            url={videoUrl}
                          />
                          <div>
                            <div className="camera-name">Camera name: {cameraName}</div>
                            <div className="camera-name">{streamName}</div>
                          </div>
                        </Grid>
                      )}
                    </Draggable>
                  );
                })}
                {provided.placeholder}
              </Grid>
            )}
          </Droppable>
        </DragDropContext>
      </div>
    </div>
  );
}

// Reference :
// https://www.npmjs.com/package/react-player
// https://openbase.com/categories/js/best-react-video-player-libraries?orderBy=RECOMMENDED&
// https://github.com/CookPete/react-player/blob/HEAD/src/demo/App.js
// https://www.freecodecamp.org/news/how-to-add-drag-and-drop-in-react-with-react-beautiful-dnd/