import React from 'react';
import { TextField, Grid, Button }  from '@material-ui/core';
import { Link } from 'react-router-dom';
import '../../styles/Auth.scss';

export default function ResetPassword () {
  return (
    <div className="vms-auth">
      <div className="vms-pt-md vms-px-md">
        <Link className="vms-white" to="/">Back to Dashboard</Link>
      </div>
      <div className="vms-auth-overlay">
        <div className="vms-title vms-text-center">Welcome to VMS WEB</div>
        <div className="vms-line"></div>
        <div className="vms-subtitle">RESET PASSWORD</div>
        <form className="vms-form" noValidate autoComplete="off">
          <Grid container spacing={3}>
            <Grid item lg={12} xs={12}>
              <TextField className="vms-full-width vms-field" type="password" placeholder="New Password" />
            </Grid>
            <Grid item lg={12} xs={12}>
              <TextField className="vms-full-width vms-field" type="password" placeholder="Verify New Password" />
            </Grid>
            <Grid item lg={12} xs={12}>
            <Button className="vms-full-width vms-auth-btn" variant="contained" color="primary">SUBMIT</Button>
            </Grid>
          </Grid>
        </form>
      </div>
    </div>
  );
}