import React, {useState} from 'react';
import '../../styles/RetrieveFilter.scss';
import { Grid, TextField, MenuItem } from '@material-ui/core';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
// https://material-ui-pickers.dev/getting-started/installation

const dummyUser = [
  {
    value: 'A',
    label: 'Group A',
  },
  {
    value: 'B',
    label: 'Group B',
  },
  {
    value: 'C',
    label: 'Group C',
  },
  {
    value: 'D',
    label: 'Group D',
  },
];


export default function RetrieveFilter() {
  


  
  const [selectedDate, setSelectedDate] = React.useState(new Date('2014-08-18T21:11:54'));

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  const [group, setGroup] = useState('A');

  const handleChangeGroup = (event) => {
    setGroup(event.target.value);
  };

  return (
    <div className="vms-retrieve-filter">
      <Grid className="vms-flex-items-center" container direction="row" spacing={6}>
        <Grid item lg={3} md={3}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker className="vms-full-width"
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-start"
              label="Date picker inline"
              value={selectedDate}
              onChange={handleDateChange}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item lg={3} md={3}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker className="vms-full-width"
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-end"
              label="Date picker inline"
              value={selectedDate}
              onChange={handleDateChange}
              KeyboardButtonProps={{
                'aria-label': 'change date',
              }}
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid className="pt-32" item lg={3} md={3}>
          <TextField
            required
            size="small"
            margin="dense"
            id="username"
            label="Recording Name"
            type="text"
            fullWidth
          />
        </Grid>
        <Grid className="pt-32" item lg={3} md={3}>
          <TextField
            id="groups"
            required 
            fullWidth
            select
            label="Assign User Group(s)"
            value={group}
            onChange={handleChangeGroup}
          >
            {dummyUser.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>
    </div>
  );
}