import React, {useState} from 'react';
import { IconButton, Grid, Backdrop, TextField, Button, Dialog, DialogActions, DialogContent } from '@material-ui/core';
import '../../styles/AccountSettings.scss';
import VideoPlayer from '../../components/Shared/VideoPlayer';
import { Eye, XCircle } from 'react-feather';
import { FiberManualRecordRounded, Share } from '@material-ui/icons';
import '../../styles/ViewCamera.scss';

export default function ViewCamera(props) {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const [isPreview, setPreviewOpen ] = useState(false);

  const handlePreviewOpen = () => {
    setPreviewOpen(true)
  }

  const handlePreviewClose = () => {
    setPreviewOpen(!isPreview)
  }

  return (
    <div>
      <IconButton aria-label="delete" onClick={handleClickOpen}>
        <Eye fontSize="small" className="vms-blue" />
      </IconButton>
      <Dialog className="vms-dialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className="vms-dialog-header vms-mb-md">
          <div className="vms-dialog-title">VIEW CAMERA</div>
        </div>
        <DialogContent>
          <Grid container>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Camera URL</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                value={props.url}
                type="text"
                fullWidth
              />
            </Grid>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Camera Name</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                value={props.camera}
                type="text"
                fullWidth
              />
            </Grid>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Stream Name</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                value={props.stream}
                type="text"
                fullWidth
              />
            </Grid>
            <Grid item lg={3} md={3} className="vms-flex-items-center">
              <label className="vms-label">Group</label>
            </Grid>
            <Grid item lg={9} md={9}>
              <TextField
                required
                size="small"
                margin="dense"
                value={props.group}
                type="text"
                fullWidth
              />
            </Grid>
          </Grid>   
        </DialogContent>
        <DialogActions className="vms-pb-md">
          <Button className="inter" onClick={handlePreviewOpen} color="primary">
            <b>PLAY</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>EDIT</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>REMOVE</b> 
          </Button>
          <Button className="inter" onClick={handleClose} color="primary">
            <b>CANCEL</b> 
          </Button>
        </DialogActions>
      </Dialog>
      <Backdrop className="vms-backdrop" open={isPreview} onClick={handlePreviewClose}>
        <div className="vms-video-backdrop-container play">
          <div className="vms-text-left">
            <div className="stream-label">{props.stream}</div>
            <div className="camera-label">{props.camera}</div>
          </div>
          <XCircle onClick={handlePreviewClose}/>
          <VideoPlayer url={props.url} />
          <div className="vms-flex-between vms-mt-sm">
            <div className="vms-text-left vms-flex">
              <div className="group-label">
                Assign to group : {props.group}
              </div>
              <div className="status-label">
                Status : {props.status}
              </div>
            </div>
            <div>
              <IconButton aria-label="stop">
                <FiberManualRecordRounded fontSize="large" className="action vms-red" />
              </IconButton>
              <IconButton aria-label="share" >
                <Share fontSize="large" className="vms-green action" />
              </IconButton>
            </div>
            
          </div>
        </div>
      </Backdrop>
    </div>
  );
}